<template>
    <div>
        <v-toolbar dense flat v-if="isReport">
            <v-toolbar-title class="subtitle-2 font-weight-medium">{{product.description}} - {{product.variety}}</v-toolbar-title>
                <v-spacer></v-spacer>
            <v-toolbar-title class="subtitle-2 font-weight-medium">Code : {{product.product_no}} | Country : {{product.country_of_origin}}</v-toolbar-title>
        </v-toolbar>

        <v-tabs v-model="tab" grow color="primary">
            <v-tab
            v-for="(groupType, index) in tabItems"
            :key="index"
            >
            {{ groupType }}
            </v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
        <v-tab-item>
            <v-data-table :headers="headers" :items="firstGroupNutrients" hide-default-footer disable-pagination class="elevation-1">
            </v-data-table>
        </v-tab-item>
        <v-tab-item>
            <v-data-table :headers="headers" :items="secondGroupNutrients" hide-default-footer disable-pagination class="elevation-1">
            </v-data-table>
        </v-tab-item>
        <v-tab-item>
            <v-data-table :headers="headers" :items="thirdGroupNutrients" hide-default-footer disable-pagination class="elevation-1">
            </v-data-table>
        </v-tab-item>
        <v-tab-item>
            <v-data-table :headers="headers" :items="fourthGroupNutrients" hide-default-footer disable-pagination class="elevation-1">
            </v-data-table>
        </v-tab-item>
      </v-tabs-items>
    </div>
</template>
<script>
export default {
    name:'ProductNutrition',
    props:['product'],
    data:()=>{
        return{
            headers:[
                { text: "Type", value: "typeName", sortable:false },
                { text: "Per 1", value: "per1", sortable: false },
                { text: "%RDA", value: "rda1" ,sortable: false },
                { text: "Per", value: "per2", sortable: false },
                { text: "%RDA", value: "rda2",sortable: false},
            ],
            tabItems:['Group 1', 'Group 2', 'Group 3', 'Group 4'],
            tab:null,

        }
    },
    computed:{
        isReport(){
            return this.$router.name == "NutritionReport"
        },
        allNutrients(){
            return Object.keys(this.product)
        },
        firstGroupNutrients(){
            return this.getGroupNutrients(this.group1Nutrient)
        },
        secondGroupNutrients(){
            return this.getGroupNutrients(this.group2Nutrient)
        },
        thirdGroupNutrients(){
            return this.getGroupNutrients(this.group3Nutrient)
        },
        fourthGroupNutrients(){
            return this.getGroupNutrients(this.group4Nutrient)
        },
    },
    methods:{
       

        group1Nutrient(key){
            let name=''
            let per1Key =''
            let rda1Key = ''
            let per2Key = ''
            let rda2Key = ''
            if( 
                 key.includes('protein')
                || key.includes('fat')
                || key.includes('carbohydrate')
                || key.includes('sugars')
                || key.includes('nut_fibre_')
                || key.includes('salt')
                || key.includes('saturates')
            ){
               if(key.includes('1_mg') || key.includes('1_g')){
                   let keysArray = key.split('_')
                   let type = keysArray[1]
                   name = `${type.charAt(0).toUpperCase()}${type.slice(1)}(${keysArray[3]})`
                    per1Key =  `${keysArray[0]}_${keysArray[1]}_${keysArray[2]}_${keysArray[3]}`
                    rda1Key = `${keysArray[0]}_${keysArray[1]}_${keysArray[2]}_percent_rda`
               }

            }else if(key.includes('energy')){
                if(key.includes('_prefix')){
                    let prefixArray = key.split('_')
                    let type = prefixArray[1]
                    let unit = prefixArray[3]
                    let prefixIndex = prefixArray[2]
                    name = `${type.charAt(0).toUpperCase()}${type.slice(1)}(${unit.toUpperCase()})`
                    if(prefixIndex == "1"){
                       per1Key= `${prefixArray[0]}_${prefixArray[1]}_${prefixArray[2]}_${prefixArray[3]}`
                       rda1Key =  `${prefixArray[0]}_${prefixArray[1]}_${prefixArray[2]}_${prefixArray[3]}_percent_rda`
                    }
                }
            }
            return{
                type:name,
                per1: per1Key,
                per2: per2Key,
                rda1 : rda1Key,
                rda2 : rda2Key
            }
        },
        group2Nutrient(key){
            let name=''
            let per1Key =''
            let rda1Key = ''
            let per2Key = ''
            let rda2Key = ''
            if(key.includes('vitamin') 
                || key.includes('_folic_acid')
                || key.includes('_pantothenic_acid')
            ){
               if(key.includes('1_mg')){
                   let keysArray = key.split('_')
                   let type = keysArray[1]
                   name = `${type.charAt(0).toUpperCase()}${type.slice(1)} ${keysArray[2].charAt(0).toUpperCase()}${keysArray[2].slice(1)} (${keysArray[4]})`
                    per1Key =  `${keysArray[0]}_${keysArray[1]}_${keysArray[2]}_${keysArray[3]}_${keysArray[4]}`
                    rda1Key = `${keysArray[0]}_${keysArray[1]}_${keysArray[2]}_${keysArray[3]}_percent_rda`
                   
                }

            }else if( key.includes('biotin') || key.includes('thiamin') || key.includes('riboflavin')){
                if(key.includes('1_mg')){
                    let arr= key.split('_')
                    name = `${arr[1].charAt(0).toUpperCase()}${arr[1].slice(1)} (${arr[3]})`
                    per1Key =  `${arr[0]}_${arr[1]}_${arr[2]}_${arr[3]}`
                    rda1Key = `${arr[0]}_${arr[1]}_${arr[2]}_percent_rda`
                }
            }
            return{
                type:name,
                per1: per1Key,
                per2: per2Key,
                rda1 : rda1Key,
                rda2 : rda2Key
            }
        },
        group3Nutrient(key){
            let name=''
            let per1Key =''
            let rda1Key = ''
            let per2Key = ''
            let rda2Key = ''
            if(key.includes('potassium')  
                || key.includes('chloride')
                || key.includes('calcium')
                || key.includes('phosphorus')
                || key.includes('magnesium')
                || key.includes('iron')
                || key.includes('zinc')
                || key.includes('copper')
                || key.includes('manganese')
                || key.includes('fluoride')
                || key.includes('selenium')
                || key.includes('chromium')
                || key.includes('molybdenum')
                || key.includes('iodine')
                || key.includes('sodium')
            ){
               if(key.includes('1_mg')){
                   let keysArray = key.split('_')
                   let type = keysArray[1]
                   name = `${type.charAt(0).toUpperCase()}${type.slice(1)} (${keysArray[3]})`
                    per1Key =  `${keysArray[0]}_${keysArray[1]}_${keysArray[2]}_${keysArray[3]}`
                    rda1Key = `${keysArray[0]}_${keysArray[1]}_${keysArray[2]}_percent_rda`
                }

            }
            return{
                type:name,
                per1: per1Key,
                per2: per2Key,
                rda1 : rda1Key,
                rda2 : rda2Key
            }
        },
        group4Nutrient(key){
            let name=''
            let per1Key =''
            let rda1Key = ''
            let per2Key = ''
            let rda2Key = ''
            if(key.includes('starch')  
                || key.includes('fructose')
                || key.includes('sucrose')
                || key.includes('lactose')
                || key.includes('alcohol')
                || key.includes('nsp')
                || key.includes('glucose')
                || key.includes('cholesterol')
            ){
               if(key.includes('1_g')){
                    let arr= key.split('_')
                    name = `${arr[1].charAt(0).toUpperCase()}${arr[1].slice(1)} (${arr[3]})`
                    per1Key =  `${arr[0]}_${arr[1]}_${arr[2]}_${arr[3]}`
                    rda1Key = `${arr[0]}_${arr[1]}_${arr[2]}_percent_rda`
                }
            }else if( key.includes('aoac_fibre')){
                if(key.includes('1_g')){
                   let keysArray = key.split('_')
                   let type = keysArray[1]
                   name = `${type.charAt(0).toUpperCase()}${type.slice(1)} ${keysArray[2].charAt(0).toUpperCase()}${keysArray[2].slice(1)} (${keysArray[4]})`
                    per1Key =  `${keysArray[0]}_${keysArray[1]}_${keysArray[2]}_${keysArray[3]}_${keysArray[4]}`
                    rda1Key = `${keysArray[0]}_${keysArray[1]}_${keysArray[2]}_${keysArray[3]}_percent_rda`
                }
            }else if(key.includes('monofa') || key.includes('polyfa')|| key.includes('transfa')){
                if(key.includes('1_g')){
                   let keysArray = key.split('_')
                   let type = keysArray[1]
                   let nameExFa = type.slice(0,-2)
                   let nameInFa = type.slice(type.length-2, type.length)
                   name = `${nameExFa.charAt(0).toUpperCase()}${nameExFa.slice(1)} ${nameInFa.toUpperCase()} (${keysArray[3]})`
                    per1Key =  `${keysArray[0]}_${keysArray[1]}_${keysArray[2]}_${keysArray[3]}`
                    rda1Key = `${keysArray[0]}_${keysArray[1]}_${keysArray[2]}_percent_rda`
                }

            }
            return{
                type:name,
                per1: per1Key,
                per2: per2Key,
                rda1 : rda1Key,
                rda2 : rda2Key
            }
        },

        getGroupNutrients(group){
          let nutrients =[]
          let finalNutrients =[]
          this.allNutrients.forEach(element => {
              if(element.includes('nut_')){
                    let nutrientInfo = group(element)
                    if(nutrientInfo.type && nutrientInfo.per1 && nutrientInfo.rda1){
                        nutrients.push({typeName :nutrientInfo.type,per1 : nutrientInfo.per1, rda1:nutrientInfo.rda1})
                    }
                }
          });              
           nutrients.forEach((el)=>{
               let prodObj = this.product
               let per1Key = el.per1
               let keysArray = per1Key.split('_')
               let per2Key = `${keysArray[0]}_${keysArray[1]}_2_${keysArray[3]}`
               let rda1Key = el.rda1
               let rdaArray = rda1Key.split('_')
               let rda2Key = ''
               if(rdaArray[1]=="vitamin" || rdaArray[1] == "pantothenic" || rdaArray[1]=="folic" || rdaArray[1]=="aoac"){
                    rda2Key =`${rdaArray[0]}_${rdaArray[1]}_${rdaArray[2]}_2_percent_rda`
                    per2Key = `${keysArray[0]}_${keysArray[1]}_${keysArray[2]}_2_${keysArray[4]}`
                }else if(rdaArray[1]=="energy"){
                    rda2Key =`${rdaArray[0]}_${rdaArray[1]}_2_${rdaArray[3]}_percent_rda`
                }    
                else{
                   rda2Key =`${rdaArray[0]}_${rdaArray[1]}_2_percent_rda`
                   per2Key = `${keysArray[0]}_${keysArray[1]}_2_${keysArray[3]}`
                }
               let strPer1Key = per1Key.toString()
               let strRda1Key = rda1Key.toString()
               let strPer2Key = per2Key.toString()
               let strRda2Key = rda2Key.toString()
               
               finalNutrients.push({
                   typeName:el.typeName, 
                   per1:prodObj[strPer1Key] !==null ? prodObj[strPer1Key] : prodObj[strPer1Key]="0.00", 
                   rda1: prodObj[strRda1Key] !==null ? prodObj[strRda1Key] : prodObj[strRda1Key]="0.00",
                   per2: prodObj[strPer2Key]!==null ? prodObj[strPer2Key] : prodObj[strPer2Key]="0.00",
                   rda2: prodObj[strRda2Key] !==null ? prodObj[strRda2Key] : prodObj[strRda2Key]="0.00"
                })
           })
           return finalNutrients
                  
        }
    }
}
</script>