<template>
    <div>
        <v-toolbar dense flat v-if="isReport">
            <v-toolbar-title class="subtitle-2 font-weight-medium">{{product.description}} - {{product.variety}}</v-toolbar-title>
                <v-spacer></v-spacer>
            <v-toolbar-title class="subtitle-2 font-weight-medium">Code : {{product.product_no}} | Country : {{product.country_of_origin}}</v-toolbar-title>
        </v-toolbar>
        <v-data-table :headers="headers" :items="prodAllergens" hide-default-footer class="elevation-1">
            <template v-slot:item.desc="{item}">
              <span>N/A</span>
            </template>
            <template v-slot:no-data>
              <span class="subtitle-2 font-weight-medium">No Allergen Data Found</span>
            </template>
        </v-data-table>
    </div>
</template>
<script>
export default {
    name:'ProductAllergen',
    props:['product'],
    data:()=>{
        return{
            headers:[
                { text: "Allergen Type", value: "name", sortable:false },
                { text: "Contains", value: "contains", sortable: false },
                { text: "Description", value: "desc" },
            ]

        }
    },
    computed:{
        prodAllergens(){
          let arr = Object.keys(this.product)
          let allergens =[]
          arr.forEach(element => {
              if(element.includes('nut')){
                  if(!element.includes('nut_prod_') && this.product[element]){
                      allergens.push({name : this.formatAllergenKey(element), contains:this.formatAllergenValue(this.product[element])})
                  }
              }
          });
          return allergens
        },
        isReport(){
            return this.$router.name == "AllergenReport"
        }

    },
    methods:{
        formatAllergenKey(key){
            let key_str=""
            switch (key) {
                case "nut_peanuts_flag":
                key_str =  "Peanuts";
                break;

                case "nut_nuts_flag":
                key_str =  "Nuts";
                break;

                case "nut_eggs_flag":
                key_str =  "Eggs";
                break;

                case "nut_milk_flag":
                key_str =  "Milk";
                break;

                case "nut_crustaceans_flag":
                key_str =  "Crustaceans";
                break;

                case "nut_fish_flag":
                key_str =  "Fish";
                break;

                case "nut_sesame_seeds_flag":
                key_str =  "Sesame";
                break;

                case "nut_cereals_containing_gluten_flag":
                key_str =  "Gluten";
                break;

                case "nut_soya_flag":
                key_str =  "Soya";
                break;

                case "nut_celery_flag":
                key_str =  "Celery";
                break;

                case "nut_mustard_flag":
                key_str =  "Mustard";
                break;

                case "nut_sulphur_dioxide_or_sulphites_flag":
                key_str =  "Sulphur Dioxide or Sulphates";
                break;

                case "nut_lupin_flag":
                key_str =  "Lupin";
                break;

                case "nut_molluscs_flag":
                key_str =  "Molluscs";
                break;

                case "nut_vegetarians_flag":
                key_str =  "Suitable for Vegetarians";
                break;

                case "nut_vegans_flag":
                key_str =  "Suitable for Vegans";
                break;

                case "nut_coeliacs_flag":
                key_str =  "Suitable for Coeliacs";
                break;

                case "nut_diabetics_flag":
                key_str =  "Suitable for Diabetics";
                break;

                case "nut_organic_certified_flag":
                key_str =  "Organic Certified";
                break;

                case "nut_leaf_certified_flag":
                key_str =  "Leaf Certified";
                break;

                case "nut_red_tractor_certified_flag":
                key_str =  "Red Tractor Certified";
                break;

                case "nut_fair_trade_certified_flag":
                key_str =  "Fair Trade Certified";
                break;

                default:
                key_str =  "";
                break;
            }
            return key_str;
        }, 
        formatAllergenValue(val){
            val = val.toString().toLowerCase()
            let value = ""
            switch (val) {
                case "m":
                    value = "May Contain";
                    break;
                case "y":
                    value = "Yes";
                    break;
                case "x":
                    value = "Not Stated";
                    break;
                default:
                    value = "";
                    break;
            }
            return value
        },
        
    }
}
</script>